#about{
    overflow: hidden;
    padding-bottom:  30px;
}
.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}
.get_to_know{
    font-size: 20px;
    margin-bottom: 0;
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary),transparent);
    display: grid;
    place-items: center;
}

#about > h2 > span {
    position: relative;
    font-size: 40px;
    top:10px;
    color: white;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc,0 3px 0 #ccc,0 4px 0 #ccc,0 5px 0 #ccc,0 6px 0 transparent,0 7px 0 transparent,0 8px 0 transparent,0 9px 0 transparent, 0 10px 10px rgba(0, 0, 0, 0.4);
    animation: bounce 0.7s infinite alternate;
}

@keyframes bounce {
    100%{
        top: -10px;
        text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc, 0 5px 0 #ccc, 0 6px 0 #ccc, 0 7px 0 #ccc, 0 8px 0 #ccc, 0 9px 0 #ccc, 0 50px 20px rgba(0, 0, 0, 0.2);
    }
}

#about > h2 > span:nth-child(2){
    animation-delay: 0.1s;
}
#about > h2 > span:nth-child(3){
    animation-delay: 0.2s;
}
#about > h2 > span:nth-child(4){
    animation-delay: 0.3s;
}
#about > h2 > span:nth-child(5){
    animation-delay: 0.4s;
}
#about > h2 > span:nth-child(6){
    animation-delay: 0.5s;
}
#about > h2 > span:nth-child(7){
    animation-delay: 0.6s;
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(15deg);
    transition: var(--transition);

}

.about__me-image:hover{
    transform: rotate(0);
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
    font-family: 'Roboto Mono', monospace;
    
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
    box-shadow: 5px 5px rgb(36, 113, 149),
        10px 10px rgb(62, 128, 163);
    z-index: 3;

}

.about__card:hover{
    background-color: var(--color-bg-cm);
    border-color: var(--color-bg-variant);
    cursor: default;
    color: black;
}


.about__icon{
    color: var(--color-primary);
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 1rem;
}

.about__card small{
    font-size: 0.86rem;
    color: var(--color-light);
}

.about__card:hover small{
    color: var(--color-primary);
}

.about_p{
    margin-top: 25px;
    line-height: 25px;
    margin-bottom: 15px;
    color: rgb(15, 14, 14);
    font-family: 'Roboto Mono', monospace;
    background-color: var(--color-bg-cm);
    padding: 30px;
    width: 100%!important;
    border-radius: 10px;
}

.btn-s {
    box-shadow: 3px 3px rgb(68, 170, 180), -1em 0 .4em rgb(1, 107, 136);
    margin-left: 40%;
}

@media screen and (max-width : 1024px ) {
    .about__container{
        grid-template-columns: 1fr;
        gap: 2%;
    }
    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content{
        margin: 1rem 0 1.5rem;
    }
    
}
@media screen and (max-width : 600px) {
    .about__container {
            grid-template-columns:100%;
        }
    .about__me{
        width: 65%;
        
    }
    .about__cards{
        grid-template-columns: 1fr;
        gap: 1rem;
        margin-left: 5%;
        
    }
    .about__card{
        width: 90%;
    }

}