nav{
    background: rgba(240, 255, 255, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 10;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
    overflow: hidden;
}

nav a{
    background: transparent;
    padding: 0.8rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.2rem;
    transform: scale(1);
    transition: transform 1s;
}
nav a:hover{
    color: var(--color-primary);
    padding: 12px;
    border-radius: 50%;
    background: var(--color-bg);
    
}

nav a.active{
    background: var(--color-bg);
    color: var(--color-primary);
}

@media screen and (max-width : 600px) {
    nav{
        display: none;
        width: 0;
    }
}