@import url('https://fonts.googleapis.com/css2?family=Clicker+Script&family=Freehand&family=Fuzzy+Bubbles:wght@400;700&family=Pacifico&family=Poppins:wght@400;500;600;700&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Bree+Serif&family=Pacifico&family=Roboto+Mono:wght@200;300;400;500;600;700&family=Rubik+Glitch&display=swap');

*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    --color-bg-cm: rgb(251, 251, 251);
    --color-bg : #1f1f38;
    --color-bg-variant : rgb(41, 50, 61);
    --color-primary : #42baf6;
    --color-primary-variant : rgba(29, 209, 122, 0.6);
    --color-white : #fff;
    --color-light : rgba(255, 255, 255, 0.6);

    --transition : all 400ms ease;

    --container-width-lg : 75%;
    --container-width-md : 86%;
    --container-width-sm : 96%
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}



body{
    /* font-family: 'Poppins'; */
    background-color: rgb(6, 131, 138);
    /* background: linear-gradient(rgb(4, 4, 91), rgb(2, 2, 54), rgb(37, 37, 111), rgb(1, 1, 72)); */
    color: var(--color-white);
    line-height: 1.7;
    /* background-image: url(/src/assets/sssg.jpg); */
    background-size: cover;
    
    background-repeat: no-repeat;
    background-position: 100% 100%;
    width: 100vw;
}

.container{
    width: var(--container-width-lg);
    margin: 0 auto;

}

h1,h2,h3, h4, h5 {
    font-weight: 500;
    
}
 h1{
    font-size: 2.5rem;
    font-family: 'Rubik Glitch', cursive;
 }
 h2{
    font-family: 'Bree Serif', serif;
 }

section{
    margin-bottom: 10%;
    width: 100vw;
}

 section > h2 , section > h5 {
    text-align: center;
    color: var(--color-light);
 }
section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color: var(--color-primary);
}
a{
    color: var(--color-primary);
    transition: var(--transition);

}
a:hover{
    color: var(--color-white);
}
span{
    color: var(--color-primary);
}
.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-white);
    padding: 0.7rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-white);
    transform: var(--transition);
    font-family: 'Bree Serif', serif;
    z-index: 6;
    transform: scale(1);
    transition: transform 0.5s;

}

.btn:hover{
    background: var(--color-white);
    color: var(--color-bg-variant);
    border-color: var(--color-bg-variant);
    transform: scale(1.2);
}
.btn-primary{
    background: var(--color-bg-variant);
    background: solid;
    color: var(--color-white);

}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

@media screen and (max-width: 1024px ) {
    .container {
        width: var(--container-width-md);

    }
    

}

@media screen and (max-width: 600px ) {
    .container {
        width: var(--container-width-sm);
        
    
    }
    section {
        height: auto;
    }
    section > h2 {
        margin-top: 2rem;
    }

}





