header{
    height: 100vh;
    padding-top: 3rem;
    overflow: hidden;
    margin-bottom: 100px;
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

.myself{
    color: white;
}

.header-detail{
    height: auto;
    width: auto;
}

.typewritter{
    font-size: 25px;
}

.text{
    color: white;
}

