.text{
    font-family: 'Bree Serif', serif;
}

.hand_icon{
    font-size: 40px;
    transform: rotate(-10deg);
    animation: hand 0.5s infinite alternate;
}

@keyframes hand {
    100%{
        transform: rotate(30deg);
    }
}


.cta{
    margin-top: 2rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials{
    
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    font-size: 25px;
}
.header__socials::after{
    content: '';
    width: 2px;
    height: 3rem;
    background: var(--color-primary);
}
.header__socials::before{
    content: '';
    width: 2px;
    height: 3rem;
    background: var(--color-primary);
}
.header-h5{
    font-size: 20px;
    font-family: 'Bree Serif', serif;
}

.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 25rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 12rem);
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
}


h1{
    font-family: sans-serif;
    font-size: 80px;
    font-weight: 9900;
    background: linear-gradient(90deg, #ff0000, #fff000,#fff, #ffff00, #c4680c, #00ff33, #0967b4, #2a4762);
    background-size: 400%;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    /* -webkit-animation: animate 10s linear infinite alternate; */
    -webkit-animation: animate 70s linear infinite alternate;
}

