.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 10rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;
    text-shadow: -8px -10px 2px rgba(0, 0, 0, 0.6);
    font-family: 'Bree Serif', serif;
    
}


@media screen and (max-width: 1024px) {
    header{
        height: 90vh;
    }
    
}

@media screen and (max-width:600px) {
    header{
        height: 100vh;
        
    }
    .header__socials,.scroll__down{
        display: none;
    }
    .me{
        width: 16rem;
        height: 18rem;
        left: calc(50% - 8rem);
    }
    
}

