#portfolio{
    overflow: hidden;
}
.portfolio__container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
   
}


#portfolio>h2>span {
    position: relative;
    font-size: 40px;
    top: 10px;
    color: white;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc, 0 5px 0 #ccc, 0 6px 0 transparent, 0 7px 0 transparent, 0 8px 0 transparent, 0 9px 0 transparent, 0 10px 10px rgba(0, 0, 0, 0.4);
    animation: bounce 0.7s infinite alternate;
}

@keyframes bounce {
    100% {
        top: -10px;
        text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc, 0 5px 0 #ccc, 0 6px 0 #ccc, 0 7px 0 #ccc, 0 8px 0 #ccc, 0 9px 0 #ccc, 0 50px 20px rgba(0, 0, 0, 0.2);
    }
}

#portfolio>h2>span:nth-child(2) {
    animation-delay: 0.1s;
}

#portfolio>h2>span:nth-child(3) {
    animation-delay: 0.2s;
}

#portfolio>h2>span:nth-child(4) {
    animation-delay: 0.3s;
}

#portfolio>h2>span:nth-child(5) {
    animation-delay: 0.4s;
}

#portfolio>h2>span:nth-child(6) {
    animation-delay: 0.5s;
}

#portfolio>h2>span:nth-child(7) {
    animation-delay: 0.6s;
}
#portfolio>h2>span:nth-child(8) {
    animation-delay: 0.7s;
}
#portfolio>h2>span:nth-child(9) {
    animation-delay: 0.8s;
}

.portfolio__item{
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 2px solid transparent;
    transition: var(--transition);
    position: relative;
    font-family: 'Bree Serif', serif;
    
}

.portfolio__item:hover{
    border-color: 5px solid var(--color-bg-cm);
    background-color: var(--color-white);
    color: black;

}

.portfolio__item-image{
    border-radius: 1.5rem;
    overflow: hidden;
}
.portfolio__item h3{
    margin: 1.2rem 0 2rem;
}
.portfolio-item-cta{
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
}

.portfolio__item .portfolio-item-cta .right-btn{
    margin-right: 0%;
}

.btn-p {
    width: max-content;
    display: inline-block;
    color: var(--color-white);
    padding: 0.7rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-white);
    transform: var(--transition);
}
.btn-p:hover{
    transform: scale(1.2);
}
.portfolio__item:hover .btn-p{
    background-color: var(--color-bg-variant);
    border: var(--transition);
}

@media screen and (max-width: 1024px){
    .portfolio__container{
        grid-template-columns: 1fr 1fr;
    }
}

@media screen and (max-width: 600px) {
    .portfolio__container{
        grid-template-columns: 1fr;
    }
    
}