#services>h2>span {
    position: relative;
    font-size: 40px;
    top: 10px;
    color: white;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc, 0 5px 0 #ccc, 0 6px 0 transparent, 0 7px 0 transparent, 0 8px 0 transparent, 0 9px 0 transparent, 0 10px 10px rgba(0, 0, 0, 0.4);
    animation: bounce 0.7s infinite alternate;
}

@keyframes bounce {
    100% {
        top: -10px;
        text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc, 0 5px 0 #ccc, 0 6px 0 #ccc, 0 7px 0 #ccc, 0 8px 0 #ccc, 0 9px 0 #ccc, 0 50px 20px rgba(0, 0, 0, 0.2);
    }
}

#services>h2>span:nth-child(2) {
    animation-delay: 0.1s;
}

#services>h2>span:nth-child(3) {
    animation-delay: 0.2s;
}

#services>h2>span:nth-child(4) {
    animation-delay: 0.3s;
}

#services>h2>span:nth-child(5) {
    animation-delay: 0.4s;
}

#services>h2>span:nth-child(6) {
    animation-delay: 0.5s;
}

#services>h2>span:nth-child(7) {
    animation-delay: 0.6s;
}
#services>h2>span:nth-child(8) {
    animation-delay: 0.7s;
}

.main-services-wrapper {
    font-family: "Patrick Hand", cursive;

    background-position: center;
    background-size: cover;
    padding-top: 3rem;
}
.swiper{
    overflow: visible!important;
}
.swiper-container {
    width: 100%;
    padding-top: 3rem;
    padding-bottom: 3rem;
}

.swiper-slide {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    padding: 0.5rem 1rem;
    background-color: var(--color-bg-cm);
    width: 24.75rem!important;
    border-radius: 10%;
    box-shadow: -2px 0px 8px 11px rgba(36, 34, 34, 0.75);
    -webkit-box-shadow: -2px 0px 8px 11px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: -2px 0px 8px 11px rgba(0, 0, 0, 0.75);
    
}

.swiper-slide img {
    display: block;
    width: 20rem;
    height:12rem;
    justify-self: center;
    margin-top: 0.6rem;
}

h2 {
    font-family: "GFS Didot", serif;
    font-weight: bold;
    color: #0e1212;
}

P {
    color: #160f0f;
    width: 80%;
    font-size: 18px;
    font-weight: 400;
}

    
