#experience{
    overflow: hidden;
}


.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    font-family: 'Roboto Mono', monospace;
}

#experience>h2>span {
    position: relative;
    font-size: 40px;
    top: 10px;
    color: white;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc, 0 5px 0 #ccc, 0 6px 0 transparent, 0 7px 0 transparent, 0 8px 0 transparent, 0 9px 0 transparent, 0 10px 10px rgba(0, 0, 0, 0.4);
    animation: bounce 0.7s infinite alternate;
}

@keyframes bounce {
    100% {
        top: -10px;
        text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc, 0 5px 0 #ccc, 0 6px 0 #ccc, 0 7px 0 #ccc, 0 8px 0 #ccc, 0 9px 0 #ccc, 0 50px 20px rgba(0, 0, 0, 0.2);
    }
}

#experience>h2>span:nth-child(2) {
    animation-delay: 0.1s;
}

#experience>h2>span:nth-child(3) {
    animation-delay: 0.2s;
}

#experience>h2>span:nth-child(4) {
    animation-delay: 0.3s;
}

#experience>h2>span:nth-child(5) {
    animation-delay: 0.4s;
}

#experience>h2>span:nth-child(6) {
    animation-delay: 0.5s;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 3px solid transparent;
    border-color: rgb(205, 198, 185);
    transition: var(--transition);
    z-index: 1;
    box-shadow: 5px 5px rgb(36, 113, 149),
            10px 10px rgb(62, 128, 163);
}

.experience__container>div:hover {
    /* background-color: rgba(12, 64, 127, 0.3); */
    background-color: var(--color-bg-cm);
    border-color: rgb(45, 43, 43);
    cursor: default;
    color: black;
}

.experience__container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    transform: translateY(100px);
    color: var(--color-primary);
    transition: transform 0.5s;
}
.experience__container>div:hover h3{
    transform: translateY(0);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2.5rem;
    gap: 1rem;
    transform: scaleY(0);
    transition: transform 1s;
    z-index: 1;
    overflow: hidden;
}
.experience__container>div:hover .experience__content{
    transform: scaleY(1);
}

.experience__detail{
    display: flex;
    gap: 1rem;
}
.experience__details-icon{
    font-size: 20px;
    transform: rotate(0deg);
    transition: transform 2s;
}
.experience__container>div:hover .experience__content .experience__details-icon{
    transform: rotate(720deg);
}
.experience__content>div h4{
    font-size: 28px;
}


@media screen and (max-width : 1024px ){
    .experience__container{
        grid-template-columns: 1fr;
    }

    .experience__container > div {
        width: 90%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience__content{
        padding: 1rem;
    }
}

@media screen and (max-width : 600px){
    .experience__container{
        gap: 1rem;
        
    }
    .experience__detail{
        display: flex;
        flex-direction: column;
        gap: 0;
    }
}