
.testimonial__container{
    display: grid;
    grid-template-columns: 1fr;
    width: 100%;
}

#testomonial>h2>span {
    position: relative;
    font-size: 40px;
    top: 10px;
    color: white;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc, 0 5px 0 #ccc, 0 6px 0 transparent, 0 7px 0 transparent, 0 8px 0 transparent, 0 9px 0 transparent, 0 10px 10px rgba(0, 0, 0, 0.4);
    animation: bounce 0.7s infinite alternate;
}

@keyframes bounce {
    100% {
        top: -10px;
        text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc, 0 5px 0 #ccc, 0 6px 0 #ccc, 0 7px 0 #ccc, 0 8px 0 #ccc, 0 9px 0 #ccc, 0 50px 20px rgba(0, 0, 0, 0.2);
    }
}

#testomonial>h2>span:nth-child(2) {
    animation-delay: 0.1s;
}

#testomonial>h2>span:nth-child(3) {
    animation-delay: 0.2s;
}

#testomonial>h2>span:nth-child(4) {
    animation-delay: 0.3s;
}

#testomonial>h2>span:nth-child(5) {
    animation-delay: 0.4s;
}
#testomonial>h2>span:nth-child(6) {
    animation-delay: 0.5s;
}

#testomonial>h2>span:nth-child(7) {
    animation-delay: 0.0s;
}
#testomonial>h2>span:nth-child(8) {
    animation-delay: 0.0s;
}
#testomonial>h2>span:nth-child(9) {
    animation-delay: 0.1s;
}
#testomonial>h2>span:nth-child(10) {
    animation-delay: 0.2s;
}
#testomonial>h2>span:nth-child(11) {
    animation-delay: 0.3s;
}
#testomonial>h2>span:nth-child(12) {
    animation-delay: 0.4s;
}
#testomonial>h2>span:nth-child(13) {
    animation-delay: 0.5s;
}
#testomonial>h2>span:nth-child(14) {
    animation-delay: 0.5s;
}
#testomonial>h2>span:nth-child(15) {
    animation-delay: 0.6s;
}
#testomonial>h2>span:nth-child(16) {
    animation-delay: 0.7s;
}
#testomonial>h2>span:nth-child(17) {
    animation-delay: 0.8s;
}
#testomonial>h2>span:nth-child(18) {
    animation-delay: 0.9s;
}
#testomonial>h2>span:nth-child(19) {
    animation-delay: 1s;
}

.testimonial__me{
    /* display: grid;
    grid-template-columns: 1fr 1fr 1fr; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 5%;
    z-index: 9;
    position: relative;
    overflow: hidden;
    font-family: 'Roboto Mono', monospace;
    
}
.testimonial__me-image{
    width: 30%;
    height: 100%;
    position: relative;
    overflow: hidden;
}
.text-container{
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 5%;
    top: 0px;
    left: 0px;
    transform: translateX(120%);
    transition: transform 1s;
    border-radius: 10%;
    background: rgba(255, 255, 255, 0.3);
}

.testimonial__me-image>img {
    max-width: 100%;
    height: 100%;
    z-index: 3;
    border-radius: 10%;
    overflow: hidden;
}

.testimonial__me-image:hover>img{
   opacity: 0.3;
    border: 2px solid rgb(236, 236, 239);
}
.testimonial__me-image .text-container>p{
    width: 100%;
    height: 100%;
}

.testimonial__me-image:hover .text-container{
    transform: translateX(0%);
    
}
