.testimonial__cards{
    /* display: flex;
    justify-content: space-between;
    align-items: center; */
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 5%;
    margin-top: 50px;
    font-family: 'Roboto Mono', monospace;
}

.testimonial__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    margin-left: 0vh;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
    z-index: 1;

}

.testimonial__card:hover {
    background-color:var(--color-bg-cm);
    border-color: var(--color-bg-variant);
    cursor: default;
    color: black;
}

.testimonial__icon {
    color: var(--color-primary);
    font-size: 4rem;
    margin-bottom: 1rem;
    transform: rotate(0deg);
    transition: transform 1s;
}
.testimonial__card:hover .testimonial__icon{
    transform: rotate(720deg);
}



@media screen and (max-width : 1024px) {
    
    .testimonial__me {
        flex-direction: column;
    }
    .testimonial__me-image{
        width: 80%;
    }
    .text-container>p{
        font-size: 23px;
    }
    
    
    


}

@media screen and (max-width : 600px) {
    
    .testimonial__me{
        flex-direction: column;
    }
    .testimonial__me-image {
        width: 80%;
        height: 100%;
    }
    .testimonial__cards{
        flex-direction: column;
        margin-left: 5%;
    }
    .text-container>p{
        font-size: 14px;
    }
    .testimonial__card{
        width: 90%;
    }
    .testimonial__me-image .text-container>p {
        margin-top: 0px;
    }
    .testimonial__cards{
        grid-template-columns: 1fr;
    }
}