footer{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 2rem;
    overflow: hidden;
    
}

footer a{
    color: var(--color-bg);

}

.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    font-family: 'Bubbles';
    margin-bottom: 2rem ;
    display: inline-block;
    font-family: 'Bree Serif', serif;
}

.footer__logo span {
    color: var(--color-bg);
}

.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
    font-size: 20px;
    font-family: 'Bree Serif', serif;
    
}

.permalinks>li {
    transform: scale(0.8);
    transition: transform 1s;
}
.permalinks>li:hover{
    color: white;
    transform: scale(1.3);
}

.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1.5rem;
    margin-bottom: 4rem;
    ;
}

.footer__socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.9rem ;
    border-radius: 0.7rem;
    display: flex;
    border: 2px solid transparent;
    transform: scale(1);
    transition: transform 0.3s
}

.footer__socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
    transform: scale(1.2);
}

.footer__copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);
}

@media screen and (max-width : 600px) {
    footer{
        margin-top: 10rem;
    }
    .permalinks{
        flex-direction: column;
        gap: 1rem;
    }
    .footer__socials{
        margin-bottom: 2.5rem;
    }
}
.foot{
    height: auto;
    width: auto;
}