.container.contact__container{
    width: 70%;
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;
}

#contact>h2>span {
    position: relative;
    font-size: 40px;
    top: 10px;
    color: white;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc, 0 5px 0 #ccc, 0 6px 0 transparent, 0 7px 0 transparent, 0 8px 0 transparent, 0 9px 0 transparent, 0 10px 10px rgba(0, 0, 0, 0.4);
    animation: bounce 0.7s infinite alternate;
}

@keyframes bounce {
    100% {
        top: -10px;
        text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc, 0 5px 0 #ccc, 0 6px 0 #ccc, 0 7px 0 #ccc, 0 8px 0 #ccc, 0 9px 0 #ccc, 0 50px 20px rgba(0, 0, 0, 0.2);
    }
}

#contact>h2>span:nth-child(2) {
    animation-delay: 0.1s;
}

#contact>h2>span:nth-child(3) {
    animation-delay: 0.2s;
}

#contact>h2>span:nth-child(4) {
    animation-delay: 0.3s;
}

#contact>h2>span:nth-child(5) {
    animation-delay: 0.4s;
}

#contact>h2>span:nth-child(6) {
    animation-delay: 0.5s;
}

#contact>h2>span:nth-child(7) {
    animation-delay: 0.6s;
}

.contact__options{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    font-family: 'Roboto Mono', monospace;
}

.contact__option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 2px solid transparent;
    transition: var(--transition);
    z-index: 1;
    overflow: hidden;
}
.contact__option:hover{
    background-color: var(--color-bg-cm);
    border-color: var(--color-bg-variant);
    color: black;
}

.contact__option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    color: var(--color-primary);
}

.contact__option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
    transform: scale(1);
    transition: transform 0.5s;
}
.contact__option a:hover {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
    color: black;
    transform: scale(1.2);
}
.get_in_touch{
    font-size: 20px;
}

form{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    color: var(--color-light);
    font-family: 'Roboto Mono', monospace;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: var(--color-bg-variant);
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-primary);
    z-index: 1;
    font-family: 'Roboto Mono', monospace;
}
.btn-c{
    margin-left: 40%;
}

@media screen and (max-width : 1024px ) {
    .contact__container{
        grid-template-columns: 1fr;
        gap: 2rem;
    }
}

@media screen and (max-width : 600px ) {
    .container.contact__container{
        grid-template-columns: 1fr;
        width: var(--container-width-sm);
    }
}




